@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap'); 
*
{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
p
{
  font-size: 1.6rem;
  font-weight: 800;
}
h1
{
  font-size: 6rem;
}
/* ????????????????? */

.app
{
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0, 0, 0, 0.4);
  color: #fff;
}
.app::before
{
  content: '';
  background: url('./assets/sunset.jpg') no-repeat center center/cover;
  width: 100%;
  height: 100vh;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
}
.app .search
{
  text-align: center;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.1);

}
.app .search input
{
  padding: 0.7rem 1.5rem;
  font-size: 1.2rem;
  border: 1pc solid rgba(255, 255, 255, 0.8);
  border-radius: 25px;
  text-align: center;
}
::placeholder
{
  color: #8f8f8f;
  font-weight: 500;
}
.container
{
  max-width: 700px;
  height: -700px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.app .top
{
  width: 100%;
  margin: 1rem auto;
}
.app .description
{
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(270deg);
}
.app .bottom
{
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.4);
}
